*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --dark-gray: #a1a1a1;
  --black: #000;
  --white: #fff;
  --very-dark-gray: #454545;
}

html {
  font-size: 62.5%;
}

body {
  font-family: League Spartan, sans-serif;
  font-size: 1.5rem;
  font-weight: 500;
  line-height: 1;
}

.header {
  text-shadow: 0 0 1px var(--black);
  margin: 2rem 0;
  font-size: 3.2rem;
}

.heading {
  text-transform: uppercase;
  letter-spacing: 5px;
  text-shadow: 0 0 1px var(--black);
  margin-bottom: 2rem;
  font-size: 1.5rem;
}

.body-text {
  color: var(--dark-gray);
  margin-bottom: 2rem;
  font-size: 1.45rem;
  line-height: 1.5;
}

.link {
  color: var(--black);
  letter-spacing: 12px;
  text-transform: uppercase;
  font-size: 1.6rem;
  font-weight: 700;
  text-decoration: none;
}

.arrow {
  width: 4rem;
  transition: transform .25s;
}

.link:hover .arrow {
  transform: translateX(10px);
}

@media screen and (min-width: 1440px) {
  .header {
    font-size: 5rem;
  }

  .heading {
    font-size: 2rem;
  }

  .body-text {
    margin-bottom: 5rem;
    font-size: 1.8rem;
  }

  .link {
    font-size: 2.5rem;
  }
}

.hero {
  height: 360px;
  position: relative;
}

@media screen and (min-width: 1440px) {
  .hero {
    height: 100%;
  }
}

.nav {
  width: 100%;
  z-index: 1;
  padding: 3rem 2rem;
  display: flex;
  position: absolute;
  top: 0;
}

.hamburger {
  width: 2rem;
  height: 1.7rem;
  cursor: pointer;
  background-image: url("icon-hamburger.22bab25c.svg");
  background-repeat: no-repeat;
  transition: all .2s .1s;
}

.logo {
  cursor: pointer;
  margin: auto;
}

.menu {
  background-color: var(--white);
  width: 100%;
  border-radius: 0 10rem 10rem 0;
  padding: 3.2rem 1rem;
  transition: left .3s, border-radius .5s;
  position: absolute;
  top: 0;
  left: -100%;
}

.menu.open {
  border-radius: 0;
  left: 0;
}

.hamburger.open {
  z-index: 1;
  background-image: url("icon-close.bbb63671.svg");
}

@media screen and (min-width: 1440px) {
  .logo img {
    width: 10rem;
  }

  .logo {
    margin: 0;
  }

  .menu {
    background: none;
    top: 0;
    left: 15rem;
  }

  .nav__list {
    width: fit-content;
  }

  .hamburger {
    display: none;
  }
}

.nav__list {
  justify-content: right;
  align-items: center;
  list-style: none;
  display: flex;
}

.list__item a {
  color: #000;
  margin: 0 1rem;
  font-size: 1.8rem;
  text-decoration: none;
}

@media screen and (min-width: 1440px) {
  .list__item a {
    color: var(--white);
  }
}

.slider {
  height: 100%;
  flex-direction: column;
  display: flex;
  position: relative;
  overflow: hidden;
}

.slider__track {
  transition: transform .3s;
}

.slide {
  position: absolute;
  top: 0;
  bottom: 0;
}

@media screen and (min-width: 1440px) {
  .sliders__holder {
    height: 534px;
    grid-template-columns: 840px auto;
    display: grid;
  }
}

.slider__buttons {
  display: flex;
  position: absolute;
  bottom: 0;
  right: 0;
}

.btn img {
  width: 1rem;
}

.btn {
  cursor: pointer;
  background-color: #000000e6;
  border: 0;
  padding: 1rem 1.8rem;
}

.btn:hover {
  background-color: #000c;
}

@media screen and (min-width: 1440px) {
  .slider__buttons {
    transform: translateX(100%);
  }

  .btn {
    padding: 2rem 4rem;
  }

  .btn img {
    width: 1.2rem;
  }
}

.block-slider__container {
  height: 360px;
  overflow: hidden;
}

.block-slider__track {
  transition: transform .35s cubic-bezier(1, .27, 0, 1.26) 80ms;
  position: relative;
}

.block-slider__slide {
  width: 100%;
  padding: 5rem 3rem;
  position: absolute;
}

@media screen and (min-width: 1440px) {
  .block-slider__slide {
    padding: 6rem 8rem 3rem;
  }

  .block-slider__container {
    height: 100%;
  }
}

.image-about img {
  width: 100%;
}

.about {
  padding: 5rem 3rem 2rem;
}

@media screen and (min-width: 1440px) {
  .block-about {
    grid-template-columns: repeat(3, auto);
    display: grid;
  }
}

.attribution {
  text-align: center;
  padding: 2rem;
  font-size: 1.5rem;
}

.attribution a {
  color: #3e52a3;
}

